.box {
  background-color: #f1f8fa;
  padding: 1.8rem;
  border-radius: 5px;
  margin-top: 3rem;
}

.box .text-box h1 {
  font-size: 48px;
  margin: 0;
}

.box .text-box p {
  line-height: 35px;
  font-size: 22px;
  font-weight: 200;
  text-align: justify;
  text-decoration: none;
  margin-bottom: 0;
}

.box .text-box p a {
  text-decoration: none;
  color: #4b6cc1;
}
.box .text-box p a:hover {
  text-decoration: underline;
}
